import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import TextArea from '@vkontakte/vkui/dist/components/TextArea/TextArea';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Link from '@vkontakte/vkui/dist/components/Link/Link';

const osName = platform();

const DataBase = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			{props.fetchedGroup} 
		</PanelHeader>
		<Group
		title="Запросы к базе">
            <TextArea id='SqlReq'/>
            <Button size="xl" level="2" onClick={
					(e) => {
                        var sqlite3 = require('sqlite3').verbose();
                        var db = new sqlite3.Database(':memory:');
                        
                        db.serialize(function() {
                            //db.run( document.getElementById("SqlReq").innerText);    

                            db.each(document.getElementById("SqlReq").innerText, function(err, row) {
                                document.getElementById("SqlReq").innerText += row.id + ": " + row.info;
                            });                        
                        });
                        
                        db.close();
					}
				} data-to="GoodRequest">
					Выполнить
				</Button>
		</Group>
	</Panel>
);

DataBase.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default DataBase;
